import React from "react"
import Layout from "../componets/layout"
import { Link } from "gatsby"

import img from "../data/image/notice.jpg"



export default ({children}) =>
{
    const count = 3;

    return(
        <Layout>

        <div style={{  margin:"auto", width:"100%" }}>

            <div style={{ margin:"auto", width:1280, textAlign:"center"}}>

        <div style={{width:"90%",marginLeft :40, marginTop :40}}>
            <strong> 전체 {count}   </strong>

            <div style={{position:"relative", fontSize:12, textAlign:"right"}}>                 
                <Link to = "tapmenu_notice" style={{textDecoration:"none", color:"black"}}>  전체 공지사항 보기</Link>                 
            </div>
        </div>


        <div style={{ marginLeft :40,marginTop : 20, height:80, width:"90%", borderTop : "1px solid #000", borderBottom : "1px solid #000"}}>
        
            <p/>
            <strong style={{fontSize:18}}>고비제품 포장예시</strong>
            <p/>
            
            <span style={{fontSize:12, color:"gray"}}> 2018.10.31</span>            
            <span style={{fontSize:12, color:"gray"}}>   16:13 </span>
            <span style={{fontSize:12, color:"gray"}}> 조회수 77</span>            
        
        </div>
        
        


        <div style={{marginLeft :40, overflow:"auto", width:"90%", height:"1200px",textAlign:"center"}}>
            
          
                <p>저희 스토어의 고비 전 제품은 몽골 매장에서 직접 사입한 정품으로,  </p>
                
                <p>구매시 아래와 같이 포장됩니다</p>

                <p>상품포장을 선택할 때와 그렇지 않을때를 비교해 보시고,</p>
 
                <p>상품포장이 필요한 경우 아래 상품 링크를 클릭해 주세요^^</p>

                <p>선물용 포장 박스를 별도 구매하실 수 있습니다</p>

                <img src={img} allgn="center" alt="detail_notice"/>
        </div>

        </div>
        </div>

        </Layout>
    )
}


